<template>
  <div id="home">
    <div class="container">
      <div class="header">
        <img src="@/assets/logo.png" alt="">
        <span @click="show=true">=</span>
      </div>
      <div class="content">
        <bannercom></bannercom>
        <router-view></router-view>
      </div>
    </div>
    <van-popup
      v-model="show"
      position="right"
      :style="{ height: '100%',width:'65%' }"
    >
      <sidecom @closeSide='closeSideF'></sidecom>
    </van-popup>
  </div>
</template>

<script>
import bannercom from '../components/banner'
import childcom from '../components/child'
import sidecom from '../components/side'
export default {
  data() {
    return {
      show:false
    };
  },
  components:{
    bannercom,
    childcom,
    sidecom
  },
  mounted(){

  },
  methods:{
    closeSideF(val){
      this.show = false
    }
  }
};
</script>

<style lang="less">
#home{
  .container{
    .header{
      height: 60px;
      img{
        height: 40px;
        padding: 10px 0 0 5px;
      }
      span{
        float: right;
        margin-top: 5px;
        font-size: 30px;
        margin-right: 1px;
        padding: 10px;
        color: #157fc2;
      }
    }
  }
}
</style>


