<template>
  <div id="banner">
    <van-swipe :autoplay="3000" indicator-color="white">
        <van-swipe-item  v-for="(item,index) in list" :key="index">
            <img :src="item.img" alt="">
        </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
export default {
  name: 'banner',
  data () {
    return {
        list:[
            {
                img:require('@/assets/banner1.jpg')
            },
            {
                img:require('@/assets/banner2.jpg')
            }
        ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less'>
#banner{
    .van-swipe{
        height: 200px;;
        .van-swipe__track{
            .van-swipe-item{
                text-align: center;
                img{
                    height: 100%;
                    width: 100%;
                    object-fit: cover;;
                }
            }
        }
    }
}
</style>



