<template>
  <div id="side">
    <ul>
        <li>
            <h3 class="top">
              <img @click="close" src="@/assets/close.png" alt="">
            </h3>
        </li>
        <li style="background:#171515;">
            <h3 style="font-weight:bold;font-size:13px;">
              菜单导航
            </h3>
        </li>
      <li v-for="(item,index) in tabs" :key="index">
          <h3 @click="item.checked=!item.checked">
              {{item.name}}
              <img :style="{ 'transform':item.checked?'rotate(90deg)':'rotate(0deg)' }" src="@/assets/jiantou.png" alt="">
          </h3>
          <ul :style="{ 'height':item.checked?'rotate(90deg)':'rotate(0deg)' }">
              <li v-show="item.checked" @click="Jump(item2)" v-for="(item2,index2) in item.childList" :key="index2">{{item2.name}}</li>
          </ul>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "side",
  data() {
    return {
      tabs: [
        {
          name: "首页",
          checked:false,
          childList: [
            {
              name: "首页",
              routername:'/home'
            },
          ]
        },
        {
          name: "关于大象",
          checked:false,
          childList: [
            {
              name: "公司简介",
              routername:'/home/intro'
            },
            {
              name: "公司荣誉",
              routername:'/home/aboutus'
            },
            {
              name: "公司公益",
              routername:'/home/aboutus'
            },
            {
              name: "招贤纳士",
              routername:'/home/aboutus'
            },
            {
              name: "联系我们",
              routername:'/home/aboutus'
            }
          ]
        },
        {
          name: "公司产品",
          checked:false,
          childList: [
            {
              name: "产品视频介绍",
              routername:'/home/aboutus'
            },
            {
              name: "产品介绍",
              routername:'/home/aboutus'
            },
            {
              name: "产品演示",
              routername:'/home/aboutus'
            }
          ]
        },
        {
          name: "核心技术",
          checked:false,
          childList: [
            {
              name: "专利认证",
              routername:'/home/aboutus'
            },
            {
              name: "核心技术1",
              routername:'/home/aboutus'
            },
            {
              name: "核心技术2",
              routername:'/home/aboutus'
            }
          ]
        },
        {
          name: "项目案例",
          checked:false,
          childList: [
            {
              name: "2013-2018年项目部分典型案例",
              routername:'/home/aboutus'
            },
            {
              name: "2019项目展示",
              routername:'/home/aboutus'
            }
          ]
        },
        {
          name: "新闻资讯",
          checked:false,
          childList: [
            {
              name: "公司新闻",
              routername:'/home/aboutus'
            },
            {
              name: "行业新闻",
              routername:'/home/aboutus'
            }
          ]
        },
        {
          name: "联系我们",
          checked:false,
          childList: [
            {
              name: "联系我们",
              routername:'/home/aboutus'
            },
          ]
        }
      ]
    };
  },
  methods:{
      close(){
          this.$emit('closeSide',false)
      },
      Jump(item2){
          this.close()
          this.$router.push(item2.routername)
      }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less'>
#side {
    height: 100%;
    >ul{
        background: #373a3b;
        height: 100%;
        >li{
            h3{
                color: #fff;
                height: 50px;
                line-height: 50px;
                padding:0 10px 0 20px;;
                font-weight: normal;
                font-size: 12px;
                // border-bottom: 1px solid #fff;
                img{
                    float: right;
                    width: 20px;
                    height: 20px;;
                    margin-top: 14px;;
                    transition: .2s all ease-in-out;
                }
            }
            .top{
                >img{
                    width: 15px;
                    height: 15px;
                    margin-top: 8px;
                    margin-right: -6px;
                    padding: 10px;
                }
            }
            >ul{
                height: auto;
                li{
                    display: block;
                    background: #e2e7eb;
                    height: 50px;
                    transition: .2s all ease-in-out;
                    text-align: center;
                    line-height: 50px;
                    padding:0 10px 0 20px;;
                    color: #333;
                    &:after{
                        content: '';
                        display: block;
                        height: 1px;
                        width: 100%;
                        background: #999;
                        transform: scaleY(0.5);
                    }
                }
            }
            &:after{
                content: '';
                display: block;
                height: 1px;
                width: 100%;
                background: #fff;
                transform: scaleY(0.1);
            }
            &:first-child,&:nth-child(2){
                &:after{
                    height: 0px;
                }
            }
        }
    }
}
</style>



