import Vue from 'vue'
import Router from 'vue-router'
import home from '@/pages/home'
import aboutus from '@/pages/about/about'
import intro from '@/pages/aboutdx/intro'

Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        name: 'home',
        component: home,
    }, {
        path: '/home',
        name: 'home',
        component: home,
        children: [{
                path: '',
                name: 'intro',
                component: intro,
            },
            {
                path: 'intro',
                name: 'intro',
                component: intro,
            },
            {
                path: 'aboutus',
                name: 'aboutus',
                component: aboutus,
            }
        ]
    }]
})


